import { computed, ref } from 'vue'
import { defineStore } from 'pinia'
import type { Game } from '@/types/Game'
import { ofetch } from 'ofetch'
import { useStorage } from '@vueuse/core'
import HavokPhysics, { type HavokPhysicsWithBindings } from '@babylonjs/havok'
import { shuffleArray } from '@/utils/Shuffle'
import { Howl } from 'howler'
import MenuAudio from '@/assets/menuaudio.mp3'

export const useGameStore = defineStore('counter', () => {
  const game = ref<Game | null>(null)
  const havokPhysics = ref<HavokPhysicsWithBindings | null>(null)
  const isGameLoading = ref<boolean>(false)
  const isGameLoaded = ref<boolean>(false)

  const totalGamesPlayed = ref<number>(0)
  const gameWon = ref<boolean>(false)

  const playedLevels = useStorage<number[]>('playedLevels', [])
  const levelPool = useStorage<number[]>('levelPool', [])
  const currentLevelId = ref<number>(-1)
  const sound = new Howl({
    src: [MenuAudio],
    preload: true,
    loop: true,
    volume: 0.1
  })
  const installPrompt = ref<any>(null)

  const soundPlaying = ref<boolean>(false)

  const unplayedLevels = computed(() => {
    return levelPool.value.filter((levelId) => playedLevels.value.includes(levelId) === false)
  })

  function playSound() {
    sound.play()
    soundPlaying.value = true
  }

  function pauseSound() {
    sound.pause()
    soundPlaying.value = false
  }

  function onGameWin() {
    console.log('You win!')
    gameWon.value = true
    if (playedLevels.value.includes(currentLevelId.value) === false) {
      playedLevels.value.push(currentLevelId.value)
    }
    checkIfAllPlayed()
  }

  function getNextLevel() {
    if (!game.value) return -1
    checkIfAllPlayed()

    currentLevelId.value = unplayedLevels.value[0]

    totalGamesPlayed.value++
    gameWon.value = false
  }

  function checkIfAllPlayed() {
    if (!game.value) return -1
    if (levelPool.value.length === playedLevels.value.length) {
      levelPool.value = shuffleArray(game.value.levels.map((level) => level.levelId))
      playedLevels.value = []
      console.log('All levels played, reshuffling')
    }
  }

  const getLevelById = (levelId: number) => {
    return game.value?.levels.find((level) => level.levelId === levelId)
  }

  const currentLevel = computed(() => {
    return getLevelById(currentLevelId.value)
  })

  function isLevelPlayed(levelId: number) {
    return unplayedLevels.value.includes(levelId) === false
  }

  async function loadGame() {
    isGameLoading.value = true
    try {
      // Load concurrently
      const [gameData, havokData] = await Promise.all([fetchGame(), HavokPhysics()])
      game.value = gameData
      havokPhysics.value = havokData
      isGameLoaded.value = true
      checkIfAllPlayed()
    } catch (error) {
      console.error('Error loading game:', error)
    } finally {
      isGameLoading.value = false
    }
  }

  async function fetchGame(): Promise<Game> {
    return ofetch<Game>('./game.json')
  }

  return {
    game,
    havokPhysics,
    loadGame,
    onGameWin,
    gameWon,
    getNextLevel,
    totalGamesPlayed,
    isLevelPlayed,
    isGameLoaded,
    isGameLoading,
    unplayedLevels,
    playedLevels,
    currentLevel,
    getLevelById,
    currentLevelId,
    levelPool,
    soundPlaying,
    playSound,
    pauseSound,
    installPrompt
  }
})
