import { createRouter, createWebHashHistory } from 'vue-router'
import { gameGuard } from './gameGuard'

const router = createRouter({
  history: createWebHashHistory(import.meta.env.BASE_URL),
  routes: [
    {
      path: '/',
      name: 'MainMenu',
      component: () => import('@/views/MainMenu.vue')
    },
    {
      path: '/game',
      name: 'GameView',
      component: () => import('@/views/GameView.vue'),
      meta: { requiresGameData: true }
    },
    {
      path: '/levels',
      name: 'LevelSelectView',
      component: () => import('@/views/LevelSelectView.vue'),
      meta: { requiresGameData: true }
    },
    {
      path: '/loading',
      name: 'LoadingView',
      component: () => import('@/views/LoadingView.vue'),
      meta: { requiresGameData: false }
    },
    {
      path: '/instructions',
      name: 'InstructionsView',
      component: () => import('@/views/InstructionsView.vue'),
      meta: { requiresGameData: false }
    }
  ]
})

router.beforeEach((to, from, next) => {
  if (to.matched.some((record) => record.meta.requiresGameData)) {
    gameGuard(to, from, next)
  } else {
    next()
  }
})

export default router
